.back_to_my_courses {
    display: none; }
.view_courses {
    .learning_path {
        margin-top: 0px; } }

@media screen and (max-width: 1000px) {
    .student_dashboard {
        padding: 20px 28px;
        .learning_path,.courses_details,.completed_courses_details {
            .slick-list {
                margin-right: 0px; } } } }

@media screen and (max-width: 800px) {
    .student_dashboard {
        padding: 20px 16px;
        .my_courses {
            .heading {
                display: block;
                margin-bottom: 14px; }
            .my_progress {
                .left_div {
                    display: none; }
                .right_div {
                    padding-left: 0px;
                    padding-right: 0px;
                    .course_status {
                        display: inline-block;
                        padding: 5px 12px;
                        div {
                            margin-left: 0px; } } } }
            .learning_path {
                margin-top: 28px; }
            .tag {
                margin-top: 25px; } }
        .courses_details,.completed_courses_details {
            .slider {
                display: flex;
                flex-wrap: wrap;
                margin-right: -10px;
                .progress_bar {
                    width: calc( 50% - 10px );
                    margin-right: 10px;
                    margin-bottom: 10px; } }
            .view_btn {
                display: flex; } }
        .view_last_btn {
            margin-bottom: 50px; } }
    .back_to_my_courses {
        display: block;
        text-align: left;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important;
        font-weight: bold;
        font-size: 16px;
        color: #252525;
        height: 52px;
        &:focus {
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important; }
        span {
            margin-right: 10px; } } }



@media screen and (max-width: 400px) {
    .student_dashboard {
        .courses_details,.completed_courses_details {
            .slider {
                display: flex;
                flex-wrap: wrap;
                .progress_bar {
                    width: 100%;
                    margin-bottom: 10px; } } } } }

