.role_by_group {
    margin-top: 8px;
    .card_div {
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
        border-radius: 8px;
        overflow: hidden;
        .card_header {
            background: $bg_green;
            display: flex;
            height: 44px;
            align-items: center;
            justify-content: space-between;
            padding: 14px 21px;
            color: #ffffff;
            .role_name {
                cursor: pointer;
