                            padding-left: 15px;
                            padding-right: 8px;
                            cursor: pointer;
                            .head_div {
                                display: flex;
                                align-items: center;
                                h3 {
                                    font-size: 15px;
                                    line-height: 20px;
                                    color: #252525; }
                                span {
                                    font-size: 12px;
                                    line-height: 16px;
                                    margin-left: 26px;
                                    color: #252525; } }
                            .action_icon {
                                display: none;
                                span {
                                    width: 25px;
                                    height: 25px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    border-radius: 3px;
                                    background-color: #ffffff;
                                    margin-left: 10px;
                                    cursor: pointer; } }
                            &:hover {
                                background-color: #EAE8EF; }
                            &:hover .action_icon {
                                display: flex; } } } } } }
        .switch {
            display: inline-block;
            height: 20px;
            position: relative;
            width: 42px; }

        .switch input {
            display: none; }

        .slider {
            background-color: #000000;
            bottom: 0;
            cursor: pointer;
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            transition: .4s; }

        .slider:before {
            background-color: #fff;
            bottom: 2px;
            content: "";
            height: 17px;
            left: 1px;
            position: absolute;
            transition: .4s;
            width: 17px; }

        input:checked + .slider {
            background-color: #66bb6a; }

        input:checked + .slider:before {
            transform: translateX(22px); }

