                font-weight: normal;
                font-size: 15px; } } }
    .modal-form-cta {
        display: flex;
        justify-content: end;
        margin-right: 29px; }
    .multichoice_question_div {
        margin-bottom: 12px; } }


.form_div {
    .qulitative_question_section {
        .added_section_div {
            margin-left: 0px;
            margin-right: 0px; } }
    .after_added_questions {
        margin-bottom: 12px !important; }
    .after_added_questions {
        &::last-child {
            margin-bottom: 12px !important; } } }



.no_record {
