    padding-left: 16px;
    padding-right: 16px;
    .right_content {
        img {
            width: 51.97px;
            border-radius: 50%; }
        p {
            margin-left: 11px;
            line-height: 23px;
            font-family: $Segoe_UI;
            font-weight: 600;
            color: #FFFFFF;
            font-size: 17px;
            span {
               margin-left: 15.03px; } }
        .right_content_div {
            // border-right: 1px solid rgba(255, 255, 255, 0.2)
            margin-right: 23px;
            .input_div {
                opacity: {}
                width: 284px;
                height: 40px;
                border-radius: 5px;
                background: rgba(255, 255, 255, 0.2);
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 8px 16px;
                font-family: $Segoe_UI;
                font-size: 16px;
                line-height: 21px;
                input {
                    width: 90%;
                    border: none;
                    color: #ffffff;
