            .icon_div {
                padding-left: 25px;
                padding-right: 23px;
                .email_icon {
                    margin-right: 12px; } } }
        .profile_image {
            width: 40px;
            height: 40px;
            background: #ffffff;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $bg_green;
            font-family: $Segoe_UI_Bold;
            letter-spacing: 1px; } } }

.user_profile {
    .popover-body {
        padding: 0px !important;
        border-radius: 4px 4px 0px 0px !important;
        border: none !important;
        overflow: hidden !important;
        ul {
            li {
                width: 135px;
                a {
                    color: #252525 !important;
                    font-family: $Segoe_UI !important;
                    font-style: normal !important;
                    font-weight: 600 !important;
                    font-size: 14px !important;
                    line-height: 40px !important;
                    padding: 0px 14px !important; }
                &:hover {
                    background: #D9E8E2 !important; } } } }
    .bs-popover-auto {
        top: 3px !important; }
    .squar_box {
        width: 10px;
        height: 10px;
        background: #ffffff;
        transform: rotate(45deg);
        position: absolute;
        top: -5px;
        left: 109px; } }
.first_li {
    position: relative;
    z-index: 1;
    &:hover {
        .user_profile {
            .squar_box {
                background: #D9E8E2 !important; } } } }
@media screen and (min-width: 1000px) {
    header {
        padding-left: 60px;
        padding-right: 60px; }
    .user_profile .bs-popover-auto {
        top: 9px !important;
        left: -48px !important; } }
